.main-banner-autorize {
  .main-banner-bg {
    background: linear-gradient(270deg, rgba(212,238,251,1) 0%, rgba(212,238,251,0) 100%); }

  .bg-banner-autorize {
    background: url("../img/holod.png") no-repeat center;
    background-size: contain;
    width: 100%;
    height: 415px;
    @media (min-width: 768px) and (max-width: 1023px) {
      height: 260px; }
    @media (min-width: 414px) and (max-width: 767px) {
      height: 220px;
      background-position: center -5px; }
    @media (min-width: 320px) and (max-width: 413px) {
      height: 165px;
      width: 100%;
      background-position: 10px -5%; } }


  p {
    max-width: 225px;
    @media (min-width: 425px) and (max-width: 767px) {
      max-width: 286px; } } }

