.autorize-1 {
  /*tooltip Box*/
  .con-tooltip {
    //position: relative
    //background: #F2D1C9
    //border-radius: 9px
    //padding: 0 20px
    //margin: 10px
    //display: inline-block
    //transition: all 0.3s ease-in-out
 }    //cursor: default

  /*tooltip */

  .tooltip-min {
    visibility: hidden;
    z-index: 1;
    opacity: .40;
    width: 140px;
    height: 22px;
    padding: 0px 20px;
    background: #5592D9;
    color: #ffffff;
    //position: absolute
    top: -140%;
    left: -25%;
    border-radius: 20px;
    fonts: 16px;
    transform: translateY(9px);
    transition: all 0.3s ease-in-out;
    box-shadow: 0 0 5px rgba(85,146,217, 0.86); }

  .tooltip {
    visibility: hidden;
    z-index: 1;
    opacity: .40;
    width: 186px;
    padding: 0px 20px;
    background: #5592D9;
    color: #ffffff;
    position: absolute;
    top: -140%;
    left: -25%;
    border-radius: 20px;

    fonts: 16px;
    transform: translateY(9px);
    transition: all 0.3s ease-in-out;
    box-shadow: 0 0 5px rgba(85,146,217, 0.86); }


  /* tooltip  after*/
  .tooltip::after {
    content: " ";
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 12px 12.5px 0 12.5px;
    border-color: #5592D9 transparent transparent transparent;
    position: absolute;
    left: 40%; }

  .tooltip-max {
    position: absolute;
    bottom: -43px; }

  .con-tooltip:hover .tooltip,
  .con-tooltip:hover .tooltip-min,
  .con-tooltip:hover .tooltip-max {
    visibility: visible;
    transform: translateY(-10px);
    opacity: 1;
    transition: .3s linear;
    animation: odsoky 1s ease-in-out infinite  alternate; }


  @keyframes vvv {
    0% {
      transform: translateY(6px); }

    100% {
      transform: translateY(1px); } }

  /*hover ToolTip*/
  .left:hover {
    transform: translateX(-6px); }
  .top:hover {
    transform: translateY(-6px); }


  .bottom:hover {
    transform: translateY(0px); }

  /*bottom*/
  .bottom .tooltip {
    top: 130%;
    left: 0%; }

  .bottom .tooltip-min {
    top: 107%;
    left: 0%; }

  .bottom .tooltip::after {
    top: -12%;
    left: 30%;
    transform: rotate(180deg); }

  li {
    list-style: disc;
    margin-left: 10px; } }




