.main-banner {
  @media (max-width: 1023px) {
    position: relative;
    top: -15px;
    z-index: -20; }
  .main-banner-bg {
    @media (max-width: 1023px) {
      padding-top: 15px; }
    @media (min-width: 769px) {
      background: rgb(212,238,251);
      background: linear-gradient(270deg, rgba(212,238,251,1) 0%, rgba(212,238,251,0) 100%); }
    @media (max-width: 1024px) {
      background: rgba(148,38,143,1); } }

  .main-banner-title {
    height: 357px;
    background-image: url('../img/main-banner.png');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: right 50%;
    @media (min-width: 425px) and (max-width: 768px) {
      height: 200px;
      background-position: right bottom; }
    @media (max-width: 425px) {
      height: 200px;
      background-position: right bottom; } }
  .main-banner-title-mob {
    background: rgba(148,38,143,1); } }
