.autorize-1 {

  placeholder {
    //font-family: Roboto
    //font-style: normal
    //font-weight: normal
    //font-size: 14px
    //line-height: 16px
 }    //color: #5592D9

  span {
    font-size: 22px;
    position: relative;
    z-index: 20; }

  .container>span:after {
    content: "";
    position: absolute;
    height: 34px;
    width: 100vw;
    top: 0;
    left: 0;
    z-index: -100;
    padding-top: 0.25rem;
    padding-bottom: 0.25rem;
    background-color: rgb(85,146,217);
    border-radius: 0.25rem; }

  .-z-10 {
    z-index: -10; }

  .cards {
    width: 140px;
    height: 140px;
    @media (min-width: 320px) and (max-width: 999px) {
      width: 120px;
      height: 120px; } }


  .title {
      font-family: "RobotoRegular", sans-serif;
      font-style: normal;
      font-weight: bold;
      font-size: 22px;
      line-height: 26px;
      text-transform: uppercase;
      color: #173152; }

  p {
    font-family: "RobotoRegular", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    color: #173152; }

  .border-2 a {
    top: 0;
    left: 50%;
    transform: translate(-50%, -50%); }

  .border-2 a:after {
    content: "";
    position: absolute;
    right: .5em;
    top: 50%;
    width: 25px;
    height: 24px;
    transform: translate(0%, -50%);
    background-image: url(../img/shevron.png); } }
