.preference {
  span {
    font-size: 22px;
    position: relative;
    z-index: 20; }

  .container>span:after {
    content: "";
    position: absolute;
    height: 34px;
    width: 100vw;
    top: 0;
    left: 0;
    z-index: -100;
    padding-top: 0.25rem;
    padding-bottom: 0.25rem;
    background-color:  rgba(148, 51, 145, 1);
    border-radius: 0.25rem; }

  ul {
    list-style-type: none; }

  ul li {
    position: relative;
    padding: 0.5rem 0; }


  ul li::before {
    color: #94268F;
    content: "\2022";
    font-size: 40px;
    font-weight: 700;
    left: -30px;
    line-height: 47px;
    position: absolute;
    top: 0;
    @media (min-width: 320px) and (max-width: 768px) {
      left: -20px; }
    @media (min-width: 320px) and (max-width: 413px) {
      line-height: 34px; }
    @media (min-width: 425px) and (max-width: 768px) {
      line-height: 38px; } } }
