.main-banner-tv {
  @media (max-width: 1023px) {
    position: relative;
    top: -15px;
    z-index: -20; }
  .title {
    width: 225px;
    @media (min-width: 1024px) {
      width: 278px; } }

  .img-1 {
    @media (max-width: 413px) {
      transform: scale(1.7); }
    @media (max-width: 767px) {
      bottom: 0; }
    @media (min-width: 768px) {
      right: 0px;
      top: 10px;
      transform: scale(1.1); }
    @media (min-width: 1024px) {
      right: -80px; } }

  .img-2 {
    @media (max-width: 413px) {
      bottom: -45px; } }

  .main-banner-bg {
    background: linear-gradient(270deg, rgba(212,238,251,1) 0%, rgba(212,238,251,0) 100%);
    @media (max-width: 1023px) {
      padding-top: 15px; } }

  .main-banner-title {
    height: 412px;
    @media (min-width: 768px) and (max-width: 999px) {
      height: 280px; }
    @media (min-width: 414px) and (max-width: 767px) {
      height: 310px; }
    @media (min-width: 230px) and (max-width: 414px) {
      height: 280px; } } }
