.contacts {
  span {
    font-size: 22px;
    position: relative;
    z-index: 20;
    @media (min-width: 320px) and (max-width: 768px) {
      font-size: 14px; } }

  .container>span:after {
    content: "";
    position: absolute;
    height: 34px;
    width: 100vw;
    top: 0;
    left: 0;
    z-index: -100;
    padding-top: 0.25rem;
    padding-bottom: 0.25rem;
    background-color:  rgba(148, 51, 145, 1);
    border-radius: 0.25rem;
    @media (min-width: 320px) and (max-width: 768px) {
      height: 24px; }

    @media (min-width: 1024px) {
      background-color: transparent; } } }
