.button-show-more {
  .border-2 a {
    top: 0;
    left: 50%;
    transform: translate(-50%, -50%); }

  .border-2 a:after {
    content: "";
    position: absolute;
    right: .5em;
    top: 50%;
    width: 25px;
    height: 24px;
    transform: translate(0%, -50%);
    transform: rotate(0deg) translate(0, -50%);
    background-image: url(../img/shevron.png); } }
