.burger {
  position: absolute;
  right: 10px;
  top: -6px;
  width: 0px;
  height: 3px;
  cursor: pointer; }

.menu-burger ul li {
  border-left: 0px !important;
  border-right: 0px !important;
  position: relative;
  line-height: 1;
  padding: .75rem 0; }


.menu-burger ul li:after {
  content: "";
  width: 100%;
  position: absolute;
  left: 0;
  bottom: -5px;
  height: 5px;
  background: #94268F;
  opacity: 0.4;
  background: linear-gradient(180deg, rgba(68, 10, 164, 0.8) 26%, rgba(191, 169, 227, 0.25) 71%); }


.burger span, .burger span:before, .burger span:after {
  width: 23px;
  position: absolute;
  height: 3px;
  background: #943391;
  display: block;
  transition: .3s;
  border-radius: 5px; }

.burger span:before {
  content: "";
  top: -6px;
  transition: top 300ms 300ms, transform 300ms cubic-bezier(0.23, 1, 0.32, 1); }

.burger span {
  top: 20px; }

.burger span:after {
  content: "";
  bottom: -6px;
  transition: bottom 300ms 300ms, transform 300ms cubic-bezier(0.23, 1, 0.32, 1); }

.burger span.active:before {
  content: "";
  top: 0px;
  transform: rotate(45deg);
  transition: top 300ms, transform 300ms 300ms cubic-bezier(0.68, -0.55, 0.265, 1.55); }

.burger span.active:after {
  content: "";
  bottom: 0;
  transform: rotate(-45deg);
  transition: bottom 300ms, transform 300ms 300ms cubic-bezier(0.68, -0.55, 0.265, 1.55); }

.burger span.active {
  background: transparent; }


.menu-burger {
  top: 55px;
  width: 360px;
  min-height: 710px;
  background: #9113AC;
  border-top-left-radius: 18px;
  border-bottom-left-radius: 18px;
  position: absolute;
  right: -360px;
  z-index: 100;
  transition: right 500ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
  background: rgb(149,41,144);
  background: linear-gradient(0deg, rgba(149,41,144,1) 2%, rgba(161,32,189,1) 100%);
  @media (min-width: 320px) and (max-width: 360px) {
    width: calc(100vw*0.8); }

  .span-1 {
    line-height: 1.6;
    font-size: 12px; } }

.menu-burger.block {
  display: block; }

.menu-burger.animate {
  right: 0; }



.bg-phone {
  position: relative; }

.bg-phone:before {
  content: "";
  position: absolute;
  width: 24px;
  height: 21px;
  top: 0;
  transform: translate(0, 100%);
  left: 32px;
  background: url(../img/phone.png) no-repeat left center; }

